import React from 'react';
import ReactDOM from 'react-dom';
import AppRouter from './routers/AppRouter'
import LoginBox from './components/LoginBox'
//import './styles/styles.scss';


class ApplicationComponent extends React.Component
{
    constructor(props){
        super(props);
        this.handleAfterLogin = this.handleAfterLogin.bind(this);
        this.state ={
            email: undefined,
            isActive: false,
            roles: [],
            loginSuccess: false
        };
    }
    

    handleAfterLogin(data) 
    {
        this.setState(() => {
            return {
                email: data.email,
                isActive: data.isActive,
                roles: data.role,
                loginSuccess: true
                }
        });
    }

    componentDidMount(){
        try{
            const json = localStorage.getItem('options');
            const options = JSON.parse(json);
            if(options){
                this.setState(() => ({options : options}));
            }
            
        }catch(e){
            //do not do anything.
        }
    }
    componentDidUpdate(prevProps, prevState){
        /*if(prevState.options.length != this.state.options.length){
            const json = JSON.stringify(this.state.options);
            localStorage.setItem('options',json);
        }*/
    }

    render(){    
        return (
            <div>
                {this.state.loginSuccess ? <AppRouter /> : <LoginBox handleAfterLogin={this.handleAfterLogin} />}

            </div>
        );
    }
}

var app = ReactDOM.render(<ApplicationComponent />, document.getElementById('app'))



